import _ from 'lodash';
import $ from 'jquery';
//var GoogleSpreadsheet = require('google-spreadsheet');


function get_init(){
    return [[false, false, false, false, false, false, false], [false, false, false, false], [false, false, false], [false, false],
        [false,false,false,false,false,false,false,false,false,false,""],[false, false, false, false, false, false, false]];
}

function stringify_answers(a){
    return JSON.stringify(a);
}

class TopLevel extends React.Component {
    constructor() {
        super();
        const initialAns = get_init();
        this.initialAns = initialAns;
        this.state = {
            showAll: false,
            rowIndex: 0,
            answers: initialAns,
            answers_history: [],
            myFirebaseRef: null
        };
        this.pos_list = {
            '0': _.chunk([92, 828, 229, 828, 372, 828, 513, 828, 92, 850, 229, 850, 372, 850], 2),
            '1': _.chunk([168, 920, 310, 920, 580, 920, 681, 920], 2),
            '2': _.chunk([168, 942, 229, 942, 290, 942], 2),
            '3': _.chunk([92, 1010, 230, 1010], 2),
            '4': _.chunk([92,1078,230,1078,385,1078,92,1102,210,1102,368,1102,92,1125,210,1125,424,1125,90,1146],2),
            '5': _.chunk([92,1216,230,1216,372,1216,514,1216,92,1238,230,1238,372,1238],2),
        };
        this.ypos_list = [806, 918, 942, 988, 1054, 1190, 1285];
        this.multiple = [true, false, false, false, true, true]
    }

    componentDidMount() {
        const self = this;
        $(document.body).on('keydown', function (e) {
            self.handleKeyDown(e, self);
        });
        const fb = new Firebase("https://oc-input.firebaseio.com/");
        this.setState({myFirebaseRef: fb});
        fb.on('child_added', function (childSnapshot, prevChildKey) {
            // code to handle new child.
            self.setState({answers_history: self.state.answers_history.concat([_.extend(childSnapshot.val(),{key: childSnapshot.key()})])});
        });
        fb.on('child_removed', function (childSnapshot, prevChildKey) {
            // code to handle new child.
            self.setState({answers_history: _.filter(self.state.answers_history,(h)=>{return h.key != childSnapshot.key()})});
        });
    }

    render() {
        const self = this;
        var pos_list_rect = _.chunk([616, 178, 598, 414, 419, 414, 208, 414, 454, 594, 233, 594, 33, 594], 2);
        const x = 50;
        const y = self.ypos_list[self.state.rowIndex];
        const pts_str = "" + x + "," + y + " " + (x - 20) + "," + (y - 10) + " " + (x - 20) + "," + (y + 10);
        return (<div className="row">
            <div className="col-md-8">
                <h1>アンケート入力</h1>

                <div>キーボード操作（上下カーソルで質問移動（青矢印）、数字キーで選択）、あるいはマウス操作が可能。</div>

                <svg id="q-area2">
                    <g>
                        <g>
                            {_.map(pos_list_rect, function (p, i) {
                                const selected = self.state.answers[0];
                                return <rect key={i} x={p[0]} y={p[1]} width="160" height="130" fill="pink"
                                             opacity={selected[i] ? 0.8 : 0.2} stroke={selected[i] ? "red" : "black"}
                                             strokeWidth="4" onClick={self.onClickRect.bind(self,0,i)}/>;
                            })}
                        </g>
                        <g>
                            {_.map(pos_list_rect, function (p, i) {
                                const selected = self.state.answers;
                                return <text key={i} className='num' x={p[0]+20} y={p[1]+30}>{i + 1}</text>;
                            })}

                        </g>
                    </g>
                    {_.map(self.pos_list, (v, k)=> {
                        return _.map(v, function (p, i) {
                            const kn = +k;
                            const selected = self.state.answers[kn];
                            return (<g key={i}>
                                <text className={'num'+(selected[i] ? ' selected' : '')} x={p[0]-5}
                                      y={p[1]+5}>{(i + 1) % 10}</text>
                                ;
                                <circle className='point' cx={p[0]} cy={p[1]} r="10" fill="pink"
                                        opacity={selected[i] ? 0.8 : 0.1} stroke={selected[i] ? "red" : "black"}
                                        strokeWidth="1" onClick={self.onClickRect.bind(self,kn,i)}/>
                            </g>);
                        });
                    })}
                    <foreignObject x="200" y="1135" width="200" height="40">
                        <body xmlns="http://www.w3.org/1999/xhtml">
                            <input id='genre-other' type="text" onchange={self.onKeyDownGenre.bind(self)}/>
                        </body>
                    </foreignObject>
                    <g>
                        <polygon points={pts_str} fill="blue"/>
                    </g>

                    <foreignObject x="60" y="1315" width="500" height="300">
                        <body xmlns="http://www.w3.org/1999/xhtml">
                        <textarea id='free-comment' type="text" onchange={self.onKeyDownGenre.bind(self)}/>
                        </body>
                    </foreignObject>


                </svg>
                <button className='btn btn-primary' onClick={this.enterStamp.bind(self)}>入力</button>
                <table className="table">
                    <thead>
                    <tr>
                        <th></th>
                        <th>入力時刻</th>
                        <th>訪問</th>
                        <th>Q1</th>
                        <th>Q2</th>
                        <th>Q3</th>
                        <th>Q4</th>
                        <th>Q5</th>
                    </tr>
                    </thead>
                    <tbody>
                    {_.compact(_.map(_.sortBy(self.state.answers_history, (h)=> {
                        return -new Date(h.time);
                    }), (h, i) => {
                        //console.log(h);
                        const a = h.answers;
                        if(!a) return null;
                        const visit = a.visit || [];
                        const year = a.year || [];
                        const gender = a.gender || [];
                        const known = a.known || [];
                        const interest = a.interest || [];
                        const int_other = a['interest-other'] ? [a['interest-other']] :[];
                        const int_lab = a['interest-lab'] || [];

                        return <tr key={h.key}>
                            <td><a className='remove-history' onClick={self.removeHistoryItem.bind(self,h.key)}>[削除]</a></td>
                            <td>{h.time}</td>
                            <td>{visit.join(', ')}</td>
                            <td>{year}</td>
                            <td>{gender}</td>
                            <td>{known}</td>
                            <td>{interest.concat(int_other).join(', ')}</td>
                            <td>{int_lab.join(', ')}</td>
                            <td>{a.comment}</td>
                            </tr>
                    }))}

                    </tbody>
                </table>
            </div>
        </div>);
    }
    onKeyDownGenre(e) {
        if(e.keyCode==13){
            console.log(e);
            e.preventDefault();
        }
        var a = _.extend(this.state.answers);
        a[4][10] = $(e.target).val();
        console.log(a);
        this.setState({answers: a});
    }
    removeHistoryItem(k) {
        if(window.confirm('このアイテムを削除しますか？（取り消せません）')){
            this.state.myFirebaseRef.child(k).remove();
        }
    }

    mk_answer_data(answers) {
        const filter_response = (data,key,ans, names) => {
            const v = _.compact(_.chain(_.zip(ans, names)).filter((a)=> {
                return a[0]
            }).map((a)=> {
                return a[1]
            }).value());
            data[key] = v;
        };
        var dat = {};
        filter_response(dat,'visit', answers[0], ['石川', '厨川', '西澤', '田中徹', '山口', '芳賀', '田中真美']);
        filter_response(dat,'year', answers[1], ['中学生以下', '高校生', '大学生', '一般']);
        filter_response(dat,'gender', answers[2], ['男', '女', '性別その他']);
        filter_response(dat,'known', answers[3], ['知っていた', '知らなかった']);
        filter_response(dat,'interest', answers[4], ['バイオ工学', 'ロボット工学', '医用工学', '機械工学', '航空宇宙工学', '電子・電気工学','材料工学', 'マイクロ・ナノ工学', 'IT・コンピュータ','その他']);
        if(answers[4][10] != ''){dat['interest-other'] = answers[4][10]}
        filter_response(dat, 'interest-lab', answers[5], ['石川', '厨川', '西澤', '田中徹', '山口', '芳賀', '田中真美']);
        console.log(dat);
        return dat;
    }

    enterStamp() {
        const self = this;
        const fb = this.state.myFirebaseRef;
        var a = _.extend(self.state.answers);
        a[4][10] = $('#genre-other').val();
        var a2 = this.mk_answer_data(a);
        a2.comment = $('#free-comment').val();
        fb.push({
            answers: a2,
            time: new Date().toString()
        }, function (err) {
            console.log(err);
            if (!err) {
            } else {
                window.alert('通信エラー');
            }
        });
        this.setState({answers: get_init()});
    }

    onClickRect(q, i, e) {
        const a = _.extend(this.state.answers);
        this.choose(q, i);
    }

    //onClickSvg(e) {
    //    const b = e.target.getBoundingClientRect();
    //    const x = e.clientX - b.left, y = e.clientY - b.top;
    //    console.log(x,y,e);
    //}
    choose(ri, idx) {
        const self = this;
        const n = _.extend(self.state.answers);
        if (idx < n[ri].length) {
            if (self.multiple[ri]) {
                n[ri][idx] = !n[ri][idx];
            } else {
                n[ri] = _.map(_.range(n[ri].length), (_) => {
                    return false
                });
                n[ri][idx] = true;
            }
        }
        self.setState({answers: n});
    }

    handleKeyDown(e, self) {
        const keynum = +e.key == 0 ? 10 : +e.key;
        const r = self.state.rowIndex;
        if (e.key == 'ArrowUp') {
            if (r > 0) {
                self.setState({rowIndex: r - 1});
            }
            e.preventDefault();
        } else if (e.key == 'ArrowDown') {
            if (r < self.ypos_list.length - 1) {
                self.setState({rowIndex: r + 1});
            }
            e.preventDefault();
        } else if (keynum) {
            const idx = keynum - 1;
            const ri = self.state.rowIndex;
            this.choose(ri, idx);
        }

    }

}


ReactDOM.render(<TopLevel/>, document.getElementById('toplevel-container'));



